.timeline-body p {
    font-size:1.2rem;
    clear:left;
    font-family: 'Source Sans Pro', sans-serif;
}

.timeline {
    list-style: none;
    padding: 10px 0;
    position: relative;
    font-weight: 300;
}
.timeline:before {
    top: 0;
    bottom:0;
    position: absolute;
    content:" ";
    width: 2px;
    background: #000;
    left: 50%;
    margin-left: -1.5px;
}
.timeline > li {
    margin-bottom: 20px;
    position: relative;
    width: 50%;
    float: left;
    clear: left;
}
.timeline > li:before, .timeline > li:after {
    content:" ";
    display: table;
}
.timeline > li:after {
    clear: both;
}
.timeline > li:before, .timeline > li:after {
    content:" ";
    display: table;
}
.timeline > li:after {
    clear: both;
}
.timeline > li > .timeline-panel {
    width: calc(100% - 25px);
    width: -webkit-calc(100% - 25px);
    float: left;
    border: 1px solid #dcdcdc;
    background: #ffffff;
    position: relative;
}
.timeline > li > .timeline-panel:before {
    position: absolute;
    top: 26px;
    right: -15px;
    display: inline-block;
    border-top: 15px solid transparent;
    border-left: 15px solid #dcdcdc;
    border-right: 0 solid #dcdcdc;
    border-bottom: 15px solid transparent;
    content:" ";
}
.timeline > li > .timeline-panel:after {
    position: absolute;
    top: 27px;
    right: -14px;
    display: inline-block;
    border-top: 14px solid transparent;
    border-left: 14px solid #ffffff;
    border-right: 0 solid #ffffff;
    border-bottom: 14px solid transparent;
    content:" ";
}
.timeline > li > .timeline-badge {
    color: #ffffff;
    width: 24px;
    height: 24px;
    line-height: 50px;
    text-align: center;
    position: absolute;
    top: 16px;
    right: -12px;
    z-index: 100;
}
.timeline > li.timeline-inverted > .timeline-panel {
    float: right;
    box-shadow: -8px 12px 18px #888888;
}
.timeline > li.timeline-inverted > .timeline-panel:before {
    border-left-width: 0;
    border-right-width: 15px;
    left: -15px;
    right: auto;
}
.timeline > li.timeline-inverted > .timeline-panel:after {
    border-left-width: 0;
    border-right-width: 14px;
    left: -14px;
    right: auto;
}
.timeline-panel {
    box-shadow: 8px 12px 18px #888888;
    border-radius: 10px;
}
.timeline-badge > a {
    color: #000000 !important;
}
.timeline-badge a:hover {
    color: #dcdcdc !important;
}
.timeline-title {
    margin-top: 0;
    color: inherit;
}
.timeline-heading {
    padding: 1.5rem 0rem 0rem;
}
.timeline-heading h4 {
    color: #000;
    font-family: 'Raleway', serif;
    font-size: 1.7rem; 
    font-weight: 800; 
    line-height: 2rem; 
    margin: 0 0 1.5rem; 
    text-align: center; 
    text-transform: uppercase; 
}
.timeline-body > p, .timeline-body > ul {
    padding: 10px 15px;
    margin-bottom: 0;
}
.timeline-footer {
    /*color:#4679bd;*/
    color:#000000;
    clear:both;
    padding: 5px 15px;
    background-color:#e0e2e4;
    font-size:1.5rem;
}
.timeline-footer p { 
    margin-bottom: 0; 
}
.timeline-footer > a {
    cursor: pointer;
    text-decoration: none;
}
/*floats inverted panels to the right side*/
.timeline > li.timeline-inverted {
    float: right;
    clear: right;
}
/*staggers the events on the timeline*/
.timeline > li:nth-child(2) {
    margin-top: 60px;
}
/*keeps inverted panel's dots on the timeline*/
.timeline > li.timeline-inverted > .timeline-badge {
    left: -12px;
}
/*enables the time line to render*/
.no-float {
    float: none !important;
}
.timeline-img img {
    float:left;
    height:auto;
    width:90%;
    min-width: 170px;
}
.study_img{
    float:left;
    margin-left:1.2rem;
    height:auto;
    width:70%;
}

@media (max-width: 990px) {
    .time-container{
        padding:5px;
    }    
    ul.timeline:before {
        left: 0px;
    }
    ul.timeline > li {
        margin-bottom: 0px;
        position: relative;
        width:100%;
        float: left;
        clear: left;
    }
    ul.timeline > li > .timeline-panel {
        width: calc(100% - 65px);
        width: -webkit-calc(100% - 65px);
    }
    ul.timeline > li > .timeline-badge {
        left: 5px;
        margin-left: 0;
        top: 16px;
    }
    ul.timeline > li > .timeline-panel {
        float: right;
        margin-right:1rem;
        box-shadow: -8px 12px 18px #888888;
    }
    ul.timeline > li > .timeline-panel:before {
        border-left-width: 0;
        border-right-width: 15px;
        left: -15px;
        right: auto;
    }
    ul.timeline > li > .timeline-panel:after {
        border-left-width: 0;
        border-right-width: 14px;
        left: -14px;
        right: auto;
    }
    .timeline > li.timeline-inverted {
        float: left;
        clear: left;
        margin-top: 30px;
        margin-bottom: 30px;
    }
    
    .timeline > li.timeline-inverted > .timeline-badge {
        left: 5px;
    }
    .timeline-img img{
        height: auto;
        width:90%;
    }
}
